<template>
  <div id="home">
    <nav-bar class="home-nav"><div slot="center"></div></nav-bar>
    <tab-control :titles="['', '', '']"
                 @tabClick="tabClick"
                 class="tab-control"
                 v-show="isTabFixed"
                 ref="tabControl1"/>
    <scroll class="content"
            ref="scroll"
            :probe-type="3"
            @scroll="contentScroll"
            :pull-up-load="true" @pullingUp="loadMore">
<!--            @pullingUp="loadMore"-->

      <home-swiper :banners="banners" @swiperImageLoad="swiperImageLoad"/>
      <recommend-view :recommends="recommends"/>
      <feature-view></feature-view>
      <tab-control :titles="['', '', '']"
                   @tabClick="tabClick"
                   ref="tabControl2"/>
      <goods-list :goods="showGoods"></goods-list>
    </scroll>
    <back-top @click.native="backClick" v-show="isShowBackTop"/>
  </div>
</template>

<script>
//Home
import HomeSwiper from "./childComps/HomeSwiper";
import RecommendView from "./childComps/RecommendView";
import FeatureView from "./childComps/FeatureView";

//
import NavBar from "@/components/common/navbar/NavBar";
import TabControl from "@/components/content/tabControl/TabControl";
import GoodsList from "@/components/content/goods/GoodsList";
import Scroll from "@/components/common/scroll/Scroll";
import BackTop from "@/components/content/BackTop";

//
import {debounce} from "@/common/utils";
import {getHomeMultidata, getHomeData, BANNER, RECOMMEND} from "@/network/home";
import {POP, NEW, SELL} from "@/common/const";

export default {
  name: "Home",
  components: {
    HomeSwiper,
    RecommendView,
    FeatureView,
    NavBar,
    TabControl,
    GoodsList,
    Scroll,
    BackTop
  },
  data() {
    return {
      banners: [],
      recommends: [],
      goodsList: {
        'pop': {page: 1, list: []},
        'new': {page: 1, list: []},
        'sell': {page: 1, list: []},
      },
      currentType: POP,
      isShowBackTop: false,
      tabOffControl: 0,
      isTabFixed: false,
      saveY: 0
    }
  },
  // -> 
  // -> 
  created() {
    //
      this.getMultiData()
  //  
    this.getHomeProducts(POP)
    this.getHomeProducts(NEW)
    this.getHomeProducts(SELL)
  },
  activated() {
    this.$refs.scroll.scrollTo(0, this.saveY, 0)
    this.$refs.scroll.refresh()
  },
  deactivated() {
    this.saveY = this.$refs.scroll.getScrollY()
  },
  mounted() {
     // item scrollerHeight
    const refresh = debounce(this.$refs.scroll.refresh, 300)
    this.$bus.$on('itemImagesLoad', () => {
      refresh()
    })

  //  $el

  },


  updated() {
    //zhihaot1back-topupdatedscrollerHeight
    //mounted
    // this.$refs.scroll.refresh()
    // console.log('updated');
  },
  //
  computed: {
    showGoods() {
      return this.goodsList[this.currentType].list
    }
  },
  methods: {
    /**
     *
     */
    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = POP
          break
        case 1:
          this.currentType = NEW
          break
        case 2:
          this.currentType = SELL
          break
      }
      this.$refs.tabControl1.currentIndex = index
      this.$refs.tabControl2.currentIndex = index
    },
    backClick() {
      //Scroll
      this.$refs.scroll.scrollTo(0, 0)
    },
    contentScroll(position) {
      //backTop
      this.isShowBackTop = (-position.y) > 500

    //  tabControlposition: fixed
      this.isTabFixed = (-position.y) > this.tabOffControl
    },
    loadMore() {
      this.getHomeProducts(this.currentType)
    },

    swiperImageLoad() {
      this.tabOffControl = this.$refs.tabControl2.$el.offsetTop
    },

    /**
     * 
     */
    getMultiData() {
      getHomeMultidata().then(res => {
        this.banners = res.data[BANNER].list
        this.recommends = res.data[RECOMMEND].list
        // this.$refs.scroll.scroll.refresh()
        // DOM,tabOffsetTop(,updated)
        // this.$nextTick(() => {
        //   this.tabOffsetTop = this.$refs.tabControl.$el.offsetTop
        // })
      })
    },
    //
    getHomeProducts(type) {
      getHomeData(type, this.goodsList[type].page).then(res => {
        const goodsList = res.data.list;
                   // push(...)pushlist
        this.goodsList[type].list.push(...goodsList)
        this.goodsList[type].page += 1
         //  
        this.$refs.scroll.finishPullUp()
      })
    }
  }
}
</script>

