  background-color: var(--color-tint);
  color: #fff;
}

.content {
  /*margin-top: 44px;*/
  /*height: calc(100% - 96px);*/
  overflow: hidden;
  position: absolute;
  top: 44px;
  bottom: 52px;
  left: 0;
  right: 0;
}

.tab-control {
  position: relative;
  z-index: 9;
}
